import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { MatInputModule, MatNativeDateModule, MatSelectModule, MatSort, PageEvent} from '@angular/material';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import {CdkTableModule} from '@angular/cdk/table';
import {DataSource} from '@angular/cdk/collections';
import {ListComponent, SafeHtmlPipe} from '../../../list/list.component'
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';
import { FormControl, Validators, FormGroup, ValidationErrors } from '@angular/forms';
import 'rxjs/add/operator/startWith';
import 'rxjs/add/observable/merge'; 
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/filter' ;
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';


@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./item-model-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ItemModelAddComponent    {
    @HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
	
	 ItemModelForm: FormGroup;
	 modelId = 0; 
	 status;
	 itemmodel= new ItemModelData(); 
	 bootstrapFormValues = [];
	 msg = [];
 


 constructor(private http: HttpClient, private router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.modelId).subscribe(params=>{
			if(params.modelId != undefined){
				this.modelId=params.modelId; 
				}
		}); 
	
	if(this.modelId == 0){
		this.status="Add";
	}else{
		this.status="Update";
		this.populateData();
	}
	}
	
     ngOnInit() {
		this.ItemModelForm = new FormGroup({			
			'modelId' : new FormControl(''),
			'modelName' : new FormControl('', [ Validators.required]),
			'modelDesc' : new FormControl('', [ Validators.required]),
			'modelActive' : new FormControl(''),
		    'modelEntryId' : new FormControl(''),
			'modelEntryDate' : new FormControl(''),
			'modelModifiedId' : new FormControl(''),
			'modelModifiedDate' : new FormControl(''),
		});
  }
	  
	    get modelName(){ return this.ItemModelForm.get('modelName')};
	    get modelDesc(){ return this.ItemModelForm.get('modelDesc')};
	    get modelActive(){ return this.ItemModelForm.get('modelActive')};
	    get modelEntryId(){ return this.ItemModelForm.get('modelEntryId')};
	    get modelEntryDate(){ return this.ItemModelForm.get('modelEntryDate')};
	    get modelModifiedId(){ return this.ItemModelForm.get('modelModifiedId')};
	    get modelModifiedDate(){ return this.ItemModelForm.get('modelModifiedDate')};
	    get delstatusModifiedDate(){ return this.ItemModelForm.get('delstatusModifiedDate')};
	  
	  populateData(){
		const headers = new HttpHeaders({'Content-Type':'application/json'});
		
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/delstatus-data',
									JSON.stringify({ modelId :this.modelId,
												   	requestType : "update" }),
						{ headers, withCredentials: true }).subscribe(
			 										data => {
			 													this.itemmodel = data['populateData'][0];
															},
													(err) => {
																console.log("ERROR: "+err);
															 });			 
		}
	   
	 
	  
	  ngAfterViewInit(){
		    $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
	  }
 
 	 toggleAnimation(){
			this.router.navigate(['service/item-model-list']);
		 }
		   

	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.itemmodel[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
			}
		
		 if(!this.ItemModelForm.invalid){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.modelId == 0){ 
			this.itemmodel['requestType'] = 'add';
		}else{
			this.itemmodel['requestType'] = 'update';
			this.itemmodel['modelId'] = this.modelId+'';
		}
//		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/delstatus',
//									JSON.stringify(this.itemmodel),
//						{ headers, withCredentials: true }).subscribe(data => {
//			 											if(data['successmsg'].includes('Successfully')){
//														this.msg[0]='success';
//			 											this.msg[1]=data['successmsg'];
//														}else{
//														this.msg[0]='Error';
//			 											this.msg[1]=data['successmsg'];
//														}
//			 											this.toast.showtoast(this.msg);
//			 											this.toggleAnimation();
//			 											this.ItemModelForm.reset();
//														},
//														err =>{
//			 												console.log(err['error']['desc']);
//			 												this.msg[0]='Error';
//			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
//														});
			 console.log(this.itemmodel);
			
			}
	  }


  onDelete(){
		 this.itemmodel['requestType'] = 'delete';
		 this.itemmodel['modelId'] = this.modelId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/delstatus',
									JSON.stringify(this.itemmodel),
						{ headers, withCredentials: true }).subscribe(data => {
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.ItemModelForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }
}

export class ItemModelData {
	
	modelId: string ;
	modelName: string;
	modelDesc: string;
	modelActive: string = "true";
    modelEntryId:string;
	modelEntryDate:string;
    modelModifiedId:string;
    modelModifiedDate:string;
	
	 
}




